<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="introductionForm" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="" prop="big_disease_background" class="w-2/4 pl-4">
        <el-input v-model="form.big_disease_background" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="small_disease_background" class="w-2/4 pl-4">
        <el-input v-model="form.small_disease_background" placeholder="" maxlength="100" :disabled="!checked" @change="changeSmallDiseaseBackground"></el-input>
      </el-form-item>
      <el-form-item label="" prop="specific_disease" class="w-2/4 pl-4">
        <el-input v-model="form.specific_disease" placeholder="" maxlength="100" :disabled="!checked" @change="changeSpecificDisease"></el-input>
      </el-form-item>
      <el-form-item label="" prop="target_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.target_treatment_method" placeholder="CSP" maxlength="100" :disabled="!checked" @change="changeTargetcontrolTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="" prop="institution" class="w-2/4 pl-4">
        <el-input v-model="form.institution" placeholder="" maxlength="100" :disabled="!checked" @change="changeInstitution"></el-input>
      </el-form-item>
      <el-form-item label="" prop="control_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.control_treatment_method" placeholder="HSP" maxlength="100" :disabled="!checked" @change="changeControlTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="" prop="innovation" class="w-full pl-4">
        <el-input
          v-model="form.innovation"
          placeholder="1  2"
          maxlength="100"
          :disabled="!checked"
          @change="changeInnovation"
        ></el-input>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="research_title">
        <el-input v-model="form.research_title" placeholder="AI" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="other_treatment_method" class="w-full pl-4">
        <el-input v-model="form.other_treatment_method" placeholder="" :disabled="!checked" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="file">
        <file-upload ref="normalIntroductionRef" desc="meta" @getFile="getFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    FileUpload,
  },
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        big_disease_background: "",
        small_disease_background: "",
        specific_disease: "",
        target_treatment_method: "",
        institution: "",
        control_treatment_method: "",
        innovation: "",
        research_title: "",
        other_treatment_method: "",
        file: "",
      },
      rules: {
        big_disease_background: [{ required: false, message: "", trigger: "blur" }],
        small_disease_background: [{ required: false, message: "", trigger: "blur" }],
        specific_disease: [{ required: false, message: "", trigger: "blur" }],
        target_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        institution: [{ required: false, message: "", trigger: "blur" }],

        control_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        innovation: [{ required: false, message: "", trigger: "blur" }],
        research_title: [{ required: false, message: "", trigger: "blur" }],
        other_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        file: [{ required: false, message: "", trigger: ["blur", "change"] }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.big_disease_background[0].required = value
      this.rules.small_disease_background[0].required = value
      this.rules.specific_disease[0].required = value
      this.rules.target_treatment_method[0].required = value
      this.rules.institution[0].required = value

      this.rules.control_treatment_method[0].required = value
      this.rules.innovation[0].required = value
      this.rules.research_title[0].required = value
      this.rules.other_treatment_method[0].required = value
      this.rules.file[0].required = value

      if (!value) {
        this.$refs["introductionForm"].clearValidate()
      }
    },
    getResearchTitle() {
      this.form.research_title = this.form.target_treatment_method && this.form.specific_disease ? this.form.target_treatment_method + "" + this.form.specific_disease + "" : ""
    },
    changeSmallDiseaseBackground(value) {
      this.$bus.emit("from_small_disease_background", value)
    },
    changeSpecificDisease(value) {
      this.$bus.emit("from_specific_disease", value)
      this.getResearchTitle()
    },
    changeTargetcontrolTreatmentMethod(value) {
      this.$bus.emit("from_target_treatment_method", value)
    },
    changeInstitution(value) {
      this.$bus.emit("from_institution", value)
    },
    changeControlTreatmentMethod(value) {
      this.getResearchTitle()
      this.$bus.emit("from_control_treatment_method", value)
    },
    changeInnovation(value) {
      this.$bus.emit("from_innovation", value)
    },
    getFile(value) {
      this.form.file = value
      this.$nextTick(() => {
        this.$refs["introductionForm"].clearValidate("file")
      })
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["introductionForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
  },
  created() {
    this.$bus.on("from_small_disease_background", (data) => {
      this.form.small_disease_background = data
    })
    this.$bus.on("from_specific_disease", (data) => {
      this.form.specific_disease = data
      this.getResearchTitle()
    })
    this.$bus.on("from_target_treatment_method", (data) => {
      this.form.target_treatment_method = data
    })
    this.$bus.on("from_institution", (data) => {
      this.form.institution = data
    })
    this.$bus.on("from_control_treatment_method", (data) => {
      this.form.control_treatment_method = data
    })
    this.$bus.on("from_innovation", (data) => {
      this.form.innovation = data
    })
  },
}
</script>
