<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="discussForm" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="" prop="small_disease_background" class="w-2/4 pl-4">
        <el-input v-model="form.small_disease_background" placeholder="" maxlength="100" @change="changeSmallDiseaseBackground" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="specific_disease" class="w-2/4 pl-4">
        <el-input v-model="form.specific_disease" placeholder="" maxlength="100" @change="changeSpecificDisease" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="target_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.target_treatment_method" placeholder="(CSP)" maxlength="100" @change="changeTargetcontrol_treatment_method" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="control_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.control_treatment_method" placeholder="(HSP)" maxlength="100" @change="changeControlTreatmentMethod" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="future_direction" class="w-full pl-4">
        <el-input v-model="form.future_direction" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="innovation" class="w-full pl-4">
        <el-input v-model="form.innovation" placeholder="" maxlength="100" @change="changeInnovation" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="efficacy_comparison_table">
        <file-upload :showDownloadBtn="true" ref="discussComparisonUploadRef" templateUrl="/static/.xlsx" @getFile="getFile"></file-upload>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="discussion_lit_table">
        <file-upload ref="discussFileRef" desc="" countContent=" 5-10 " @getFile="getFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import FileUpload from "@/views/components/FileUpload.vue"
export default {
  components: {
    FileUpload,
  },
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        small_disease_background: "",
        specific_disease: "",
        target_treatment_method: "",
        control_treatment_method: "",
        future_direction: "",
        innovation: "",
        efficacy_comparison_table: "",
        discussion_lit_table: "",
      },
      rules: {
        small_disease_background: [{ required: false, message: "", trigger: "blur" }],
        specific_disease: [{ required: false, message: "", trigger: "blur" }],
        target_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        control_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        future_direction: [{ required: false, message: "", trigger: "blur" }],
        innovation: [{ required: false, message: "", trigger: "blur" }],
        efficacy_comparison_table: [{ required: false, message: "", trigger: "blur" }],
        discussion_lit_table: [{ required: false, message: "", trigger: "blur" }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.small_disease_background[0].required = value
      this.rules.specific_disease[0].required = value
      this.rules.target_treatment_method[0].required = value
      this.rules.control_treatment_method[0].required = value
      this.rules.future_direction[0].required = value
      this.rules.innovation[0].required = value
      this.rules.efficacy_comparison_table[0].required = value
      this.rules.discussion_lit_table[0].required = value

      if (!value) {
        this.$refs["discussForm"].clearValidate()
      }
    },
    changeSmallDiseaseBackground(value) {
      this.$bus.emit("from_small_disease_background", value)
    },
    changeTargetcontrol_treatment_method(value) {
      this.$bus.emit("from_target_treatment_method", value)
    },
    changeControlTreatmentMethod(value) {
      this.$bus.emit("from_control_treatment_method", value)
    },
    changeSpecificDisease(value) {
      this.$bus.emit("from_specific_disease", value)
    },
    changeInnovation(value) {
      this.$bus.emit("from_innovation", value)
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["discussForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
    getFile(value) {
      this.form.file = value
    },
  },
  created() {
    this.$bus.on("from_small_disease_background", (data) => {
      this.form.small_disease_background = data
    })
    this.$bus.on("from_target_treatment_method", (data) => {
      this.form.target_treatment_method = data
    })
    this.$bus.on("from_control_treatment_method", (data) => {
      this.form.control_treatment_method = data
    })
    this.$bus.on("from_specific_disease", (data) => {
      this.form.specific_disease = data
    })
    this.$bus.on("from_innovation", (data) => {
      this.form.innovation = data
    })
  },
}
</script>
