<template>
  <div>
    <el-form label-position="top" label-width="80px" :rules="rules" ref="materialsForm" :model="form" class="flex flex-wrap pr-4">
      <el-form-item label="" prop="institution" class="w-2/4 pl-4">
        <el-input v-model="form.institution" placeholder="" maxlength="100" :disabled="!checked" @change="changeInstitution"></el-input>
      </el-form-item>
      <el-form-item label="" prop="case_data_time" class="w-2/4 pl-4">
        <el-input v-model="form.case_data_time" placeholder="20141-20226" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="target_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.target_treatment_method" placeholder="(CSP)" maxlength="100" @change="changeTargetcontrol_treatment_method" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="exposed_group_size" class="w-2/4 pl-4">
        <el-input v-model.number="form.exposed_group_size" placeholder="120" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="control_treatment_method" class="w-2/4 pl-4">
        <el-input v-model="form.control_treatment_method" placeholder="(HSP)" maxlength="100" :disabled="!checked" @change="changeControlTreatmentMethod"></el-input>
      </el-form-item>
      <el-form-item label="" prop="control_group_size" class="w-2/4 pl-4">
        <el-input v-model.number="form.control_group_size" placeholder="110" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="inclusion_criteria" class="w-2/4 pl-4">
        <el-input v-model="form.inclusion_criteria" placeholder="20-65" :disabled="!checked" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="" prop="data_collection_methods" class="w-2/4 pl-4">
        <el-input v-model="form.data_collection_methods" placeholder="" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="ethics_review_number" class="w-2/4 pl-4">
        <el-input v-model="form.ethics_review_number" placeholder="4564364374" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="exclusion_criteria" class="w-2/4 pl-4">
        <el-input v-model="form.exclusion_criteria" placeholder="" :disabled="!checked" maxlength="100"></el-input>
      </el-form-item>
      <el-form-item label="" prop="statistical_tools" class="w-2/4 pl-4">
        <el-input v-model="form.statistical_tools" placeholder="R" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="missing_value_tools" class="w-2/4 pl-4">
        <el-input v-model="form.missing_value_tools" placeholder="Pythonsklearn" maxlength="100" :disabled="!checked"></el-input>
      </el-form-item>
      <el-form-item label="" prop="missing_value_method" class="w-full pl-4">
        <el-input
          v-model="form.missing_value_method"
          placeholder=";"
          maxlength="100"
          :disabled="!checked"
        ></el-input>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="general_data">
        <list-materials ref="generalDataRef" :checked="checked" @getMaterialsList="getGeneralMaterialsList"></list-materials>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="efficacy_observation_indices">
        <list-materials :showAddBtn="true" ref="efficacyObservationIndicesRef" :checked="checked" @getMaterialsList="getEfficacyMaterialsList"></list-materials>
      </el-form-item>
      <el-form-item label="" class="w-full pl-4" prop="file">
        <file-upload ref="materialsUploadRef" desc="1-4" @getFile="getFile"></file-upload>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { rule } from "postcss"
import ListMaterials from "./ListMaterials.vue"
import FileUpload from "@/views/components/FileUpload.vue"

export default {
  components: {
    ListMaterials,
    FileUpload,
  },
  props: {
    checked: { type: Boolean, default: false },
  },
  data() {
    return {
      form: {
        institution: "",
        case_data_time: "",
        target_treatment_method: "",
        exposed_group_size: "",
        control_treatment_method: "",
        control_group_size: "",
        inclusion_criteria: "20-65",
        data_collection_methods: "",
        ethics_review_number: "",
        exclusion_criteria: "",
        statistical_tools: "R",
        missing_value_tools: "Pythonsklearn",
        missing_value_method: ";",
        general_data: "",
        efficacy_observation_indices: "",
        file: "",
      },
      rules: {
        institution: [{ required: false, message: "", trigger: "blur" }],
        case_data_time: [{ required: false, message: "", trigger: "blur" }],
        target_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        exposed_group_size: [
          { required: false, message: "", trigger: "blur" },
          { type: "number", message: "", trigger: "blur" },
        ],
        control_treatment_method: [{ required: false, message: "", trigger: "blur" }],
        control_group_size: [
          { required: false, message: "", trigger: "blur" },
          { type: "number", message: "", trigger: "blur" },
        ],
        inclusion_criteria: [{ required: false, message: "", trigger: "blur" }],
        data_collection_methods: [{ required: false, message: "", trigger: "blur" }],
        ethics_review_number: [{ required: false, message: "", trigger: "blur" }],
        exclusion_criteria: [{ required: false, message: "", trigger: "blur" }],
        statistical_tools: [{ required: false, message: "", trigger: "blur" }],
        missing_value_tools: [{ required: false, message: "", trigger: "blur" }],
        missing_value_method: [{ required: false, message: "", trigger: "blur" }],
        general_data: [{ required: false, message: "", trigger: ["blur", "change"] }],
        efficacy_observation_indices: [{ required: false, message: "", trigger: ["blur", "change"] }],
        file: [{ required: false, message: "", trigger: ["blur", "change"] }],
      },
    }
  },
  methods: {
    resetRequiredForm(value) {
      this.rules.institution[0].required = value
      this.rules.case_data_time[0].required = value
      this.rules.target_treatment_method[0].required = value
      this.rules.exposed_group_size[0].required = value
      this.rules.control_treatment_method[0].required = value
      this.rules.control_group_size[0].required = value
      this.rules.inclusion_criteria[0].required = value
      this.rules.data_collection_methods[0].required = value
      this.rules.ethics_review_number[0].required = value
      this.rules.exclusion_criteria[0].required = value
      this.rules.statistical_tools[0].required = value
      this.rules.missing_value_tools[0].required = value
      this.rules.missing_value_method[0].required = value
      this.rules.general_data[0].required = value
      this.rules.efficacy_observation_indices[0].required = value
      this.rules.file[0].required = value

      if (!value) {
        this.$refs["materialsForm"].clearValidate()
      }
    },
    changeInstitution(value) {
      this.$bus.emit("from_institution", value)
    },
    changeTargetcontrol_treatment_method(value) {
      this.$bus.emit("from_target_treatment_method", value)
    },
    changeControlTreatmentMethod(value) {
      this.$bus.emit("from_control_treatment_method", value)
    },
    getGeneralMaterialsList(val) {
      this.form.general_data = val
    },
    getEfficacyMaterialsList(val) {
      this.form.efficacy_observation_indices = val
    },
    getFile(val) {
      this.form.file = val
    },
    formCheck() {
      return new Promise((resolve, reject) => {
        this.$refs["materialsForm"].validate((valid) => {
          if (valid) {
            console.log("role submit!")
            resolve(this.form)
          } else {
            console.log("error role sumbit!")
            reject()
          }
        })
      })
    },
  },
  created() {
    this.$bus.on("from_institution", (data) => {
      this.form.institution = data
    })
    this.$bus.on("from_target_treatment_method", (data) => {
      this.form.target_treatment_method = data
    })
    this.$bus.on("from_control_treatment_method", (data) => {
      this.form.control_treatment_method = data
    })

    this.$nextTick(() => {
      const defaultGeneralData = [
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "",
        },
        {
          id: "",
          content: "(SBP)(DBP)",
        },
        {
          id: "",
          content: "",
        },
      ]
      this.$refs.generalDataRef.list = defaultGeneralData
      this.form.general_data = defaultGeneralData
      const defaultEfficacyObservationIndices = [
        {
          id: "1",
          content: "",
        },
        {
          id: "2",
          content: "24",
        },
        {
          id: "3",
          content: "()",
        },
        {
          id: "4",
          content: "()",
        },
        {
          id: "5",
          content: "",
        },
        {
          id: "6",
          content: "3",
        },
      ]
      this.$refs.efficacyObservationIndicesRef.list = defaultEfficacyObservationIndices
      this.form.efficacy_observation_indices = defaultEfficacyObservationIndices
    })
  },
}
</script>
